import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader';
import {SVGLoader} from 'three/examples/jsm/loaders/SVGLoader';
import {FontLoader} from 'three/examples/jsm/loaders/FontLoader';
import {TextGeometry} from 'three/examples/jsm/geometries/TextGeometry'
import gsap from 'gsap';
import CustomEase from 'gsap/CustomEase';

gsap.registerPlugin(CustomEase)
/**
 * Base
 */
// Debug


const gltfloader = new GLTFLoader();
const svgloader = new SVGLoader();
const fontLoader = new FontLoader();
// const gui = new dat.GUI()

// Canvas
const canvas = document.querySelector('canvas.webgl');
const darkMode = document.querySelector('.darkCheck');
const paragraph = document.querySelector('p');

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color('#fff')

/**
 * geometries
 */
gltfloader.load('/google.glb',function ( gltf ) {
        const Google =  gltf.scene.children[0]
        let basicMaterials = []
        let theG = Google.children.pop()
        Google.children.unshift(theG)
		scene.add( Google );   
        Google.scale.set(110,110,110)    
        Google.rotation.x = Math.PI/2
        Google.position.x = 0
        Google.position.y = 3
        Google.position.z = 0 
            Google.children.map((mesh)=>{
                return {...mesh, material: {...mesh.material, roughness: 0.4}}
            } )
            darkMode.addEventListener('change',(e)=>{
                if(e.target.checked){
                      scene.children[6].children.forEach((child)=>{
                    basicMaterials.push(child.material.color)
                    child.material.color = new THREE.Color('white')
                    paragraph.style.color = 'white'

                })
                scene.background = new THREE.Color('#202124')
                }else {
                    scene.children[6].children.forEach((child, index)=>{
                        child.material.color = basicMaterials[index]
                    })
                    scene.background = new THREE.Color('white')
                    paragraph.style.color = 'black'
                }
              
            })

	},

);

svgloader.load('/search.svg', (data)=>	{
    const paths = data.paths;
		const group = new THREE.Group();

		for ( let i = 0; i < paths.length; i ++ ) {

			const path = paths[ i ];

			const material = new THREE.MeshBasicMaterial( {
				color: '#999',
				side: THREE.DoubleSide,
				depthWrite: false
			} );

			const shapes = SVGLoader.createShapes( path );

			for ( let j = 0; j < shapes.length; j ++ ) {

				const shape = shapes[ j ];
				const geometry = new THREE.ShapeGeometry( shape );
				const mesh = new THREE.Mesh( geometry, material );
				group.add( mesh );

			}

		}
        group.scale.set(0.02,0.02)
        group.position.set(-5.7,0.23,0.35)
     
        group.rotation.z = Math.PI
		searchBar.add( group );

	})
  const fontContainer = new THREE.Group()
 scene.add(fontContainer)
 let searchFocus = false
 let searchTexte = 'type something here...'
fontLoader.load('/font/Arial_Regular.json', (font)=>{
    function getTextMesh(text, material)  {
      
        var textgeometry = new TextGeometry(
          text,
          
            {
                font,
                size: 0.30,
                height: 0,
                
            }
          
        );
        let numberMesh = new THREE.Mesh(textgeometry, material);
        // wireframe
        numberMesh.position.set(-5.3,-0.11,0.31)
        return numberMesh;
    }
    
    
    let typingStick = getTextMesh('|', new THREE.MeshBasicMaterial({color: '#333', transparent: true}))
    fontContainer.add(typingStick)
    typingStick.position.z = 0.32
    typingStick.position.y = -0.05
    typingStick.material.opacity = 0

    let searchText = getTextMesh(searchTexte, new THREE.MeshBasicMaterial({color: '#999'})) 
    searchText.geometry.computeBoundingBox()
    fontContainer.add(searchText)
    searchText.position.set(-5.3,-0.11,0.31)

    window.addEventListener('click',()=>{
        if(intersect.length){
            if(searchTexte == 'type something here...'){
                
                searchTexte = ''
                fontContainer.remove(searchText)         
                   searchText =  getTextMesh(searchTexte, new THREE.MeshBasicMaterial({color: '#333'})) 
                    
                    fontContainer.add(searchText)
            }
              searchFocus = true
       
        }else {
             searchFocus = false
        }
        if(search_intersect.length){
          gone = true
          window.removeEventListener('mousemove', whenMouseMoves)
            console.log(scene.children[6])

            scene.children[6].children.forEach((child, index)=>{
                gsap.to(child.position, {z: -0.08, delay: index/6, duration:0.8, ease :CustomEase.create("custom", "M0,0,C0.208,0,0.226,-0.148,0.314,-0.124,0.646,0.004,0.818,1.001,1,1")})
            })
            gsap.to(searchBar.position, {y: -12, delay: 1.1, duration:0.8, ease :CustomEase.create("custom", "M0,0,C0.208,0,0.226,-0.148,0.314,-0.124,0.646,0.004,0.818,1.001,1,1")})
            gsap.to(searchButton.position, {y: -12, delay: 1.1, duration:0.8, ease :CustomEase.create("custom", "M0,0,C0.208,0,0.226,-0.148,0.314,-0.124,0.646,0.004,0.818,1.001,1,1")})
            gsap.to(fontContainer.position, {y: -12, delay: 1.1, duration:0.8, ease :CustomEase.create("custom", "M0,0,C0.208,0,0.226,-0.148,0.314,-0.124,0.646,0.004,0.818,1.001,1,1")})

setTimeout(()=>{
    window.location = `https://www.google.com/search?q=${searchTexte.replace(/\s/g, '+')}`

},1900 )

       
        }
      
    })
    window.addEventListener('keydown', (e)=>{
        if(searchFocus){
            if(e.key == 'Backspace'){
               
                searchTexte = searchTexte.split('').splice(0,searchTexte.length-1).join('')
            }else if(e.key.length ==1){
                searchTexte += e.key
            }
           
            fontContainer.remove(searchText)
           
                 searchText = getTextMesh(searchTexte, new THREE.MeshBasicMaterial({color: '#333'})) 
                
                fontContainer.add(searchText)
                
        }

    })
    const buttonTextGeometry = new TextGeometry('search',
    {
        font,
        size: 0.30,
        height: 0,
        
    })
    const buttonText = new THREE.Mesh(buttonTextGeometry, new THREE.MeshBasicMaterial({color: '#777'}))

    searchButton.add(buttonText)
    buttonText.position.set(-0.4,-0.10,0.36)
})

// search bar
const searchBar = new THREE.Group()
const middleBar = new THREE.Mesh(
    new THREE.BoxGeometry(12, 1.1, 0.6),
    new THREE.MeshStandardMaterial({color: '#e6e6e6', roughness: 0.94, metalness: 0.03})
)

const leftBar = new THREE.Mesh(
    new THREE.CylinderGeometry(0.55, 0.55, 0.6, 32, 1,false , Math.PI,Math.PI),
    new THREE.MeshStandardMaterial({color: '#e6e6e6', roughness: 0.94, metalness: 0.03})
)
leftBar.rotation.x = Math.PI/2
leftBar.position.x = -6


const rightBar = new THREE.Mesh(
    new THREE.CylinderGeometry(0.55, 0.55, 0.6, 32, 1,false , Math.PI,Math.PI),
    new THREE.MeshStandardMaterial({color: '#e6e6e6', roughness: 0.94, metalness: 0.03})
)
rightBar.rotation.x = Math.PI/2
rightBar.rotation.z = Math.PI
rightBar.position.x = 6
searchBar.add(middleBar)
searchBar.add(leftBar)
searchBar.add(rightBar)


scene.add(searchBar)

// search button 
const searchButton = new THREE.Group()
const buttonMiddle = new THREE.Mesh(
    new THREE.BoxGeometry(1.5, 1, 0.6),
    new THREE.MeshStandardMaterial({roughness: 1.2})
)
const rightButton = new THREE.Mesh(
    new THREE.CylinderGeometry(0.5, 0.5, 0.6, 32, 1,false , Math.PI,Math.PI),
    new THREE.MeshStandardMaterial({ roughness: 1.2})
)
rightButton.rotation.x = Math.PI/2
rightButton.rotation.z = Math.PI
rightButton.position.x = 0.75
searchButton.add(rightButton)
searchButton.add(buttonMiddle)
scene.add(searchButton)
searchButton.position.x = 5.25
searchButton.position.z = 0.05
// lights 
const ambientLight = new THREE.AmbientLight('white', 0.5)
scene.add(ambientLight)
const pointLight = new THREE.PointLight('white', 0.6)
scene.add(pointLight)
pointLight.position.z = 10





/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}
// raycaster
const raycaster = new THREE.Raycaster()
let intersect = raycaster.intersectObject(searchBar)
let search_intersect = raycaster.intersectObject(searchButton)
const mouseCursor = new THREE.Vector2()
const whenMouseMoves = (e)=>{
    mouseCursor.x = 2*e.clientX/sizes.width -1
    mouseCursor.y = -2*(e.clientY/sizes.height)+1
    camera.position.x = (mouseCursor.x)*-10
    camera.position.y = (mouseCursor.y)*-10
    camera.position.z = (Math.cos((mouseCursor.x*mouseCursor.y)))*5+15
    pointLight.position.x = (mouseCursor.x)*5
    pointLight.position.y = (mouseCursor.y)*5
   
}
window.addEventListener('mousemove', whenMouseMoves)

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 20
scene.add(camera)

// Controls
// const controls = new OrbitControls(camera, canvas)
// controls.enableDamping = true
// some tweeks




/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
const clock = new THREE.Clock()
let currentIntersect = null
let gone
const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
raycaster.setFromCamera(mouseCursor, camera)


if(searchFocus == true){
   
    fontContainer.children[1].geometry.computeBoundingBox();  
    if(searchTexte){

        fontContainer.children[0].position.x = fontContainer.children[1].geometry.boundingBox.max.x -5.25
    }else {
        fontContainer.children[0].position.x = -5.25
    }

    if(Math.floor(elapsedTime)%2 == 0){
        fontContainer.children[0].material.opacity = 1
    }else {
        fontContainer.children[0].material.opacity = 0

    }
    
  
}else {

 if(fontContainer.children[0] != undefined)  fontContainer.children[0].material.opacity = 0
}
intersect = raycaster.intersectObject(searchBar)
if(search_intersect.length){
    
    if(currentIntersect === null){
        
        gsap.to(searchButton.position, {z:  0.5})      
        currentIntersect = search_intersect[0]
        


}}else {
    canvas.style.cursor = 'default'
    if(currentIntersect){
      
         currentIntersect = null
         gsap.to(searchButton.position, {z:  0.05})
    }
   

}
if(search_intersect.length){
    canvas.style.cursor = 'pointer'
}else if(intersect.length){
    canvas.style.cursor = 'text'
}else {
    canvas.style.cursor = 'default'
}


search_intersect = raycaster.intersectObject(searchButton)
    // Update controls
    // controls.update()
    if(!gone){

        camera.lookAt(searchBar.position)
    }
    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()